.login_body {
    display: grid;
    /* background-color: #32B126; */
    grid-template-columns: auto auto auto;
    margin-top: 20vmin;
    justify-content: center;
}

.login_top_text {
    display: grid;
    grid-column: 1/4;
    justify-content: center;
    font-family: var(--font-family-Comfortaa);
    font-size: 6.5vmin;
    text-transform: capitalize;
    color: var(--color-text);
}

.email_input:focus,
.password_input:focus {
    border: 0.6vmin solid;
    border-top-color: #32B126;
    border-right-color: #32B126;
    border-bottom-color: #24A2E9;
    border-left-color: #24A2E9;
}



.email_input {
    display: grid;
    margin-top: 7vmin;
    margin-left: auto;
    margin-right: auto;
    grid-column: 1/4;
    outline: 0;
    padding: 3vmin;
    border: 0.5vmin solid #A4BFA2;
    border-radius: 3.5vmin;
    height: 9vmin;
    width: 64vmin;
    font-family: var(--font-family-Urbanist);
    font-size: 3.9vmin;
}

.password_input {
    grid-column: 1/4;
    display: grid;
    margin-top: 5vmin;
    margin-left: auto;
    margin-right: auto;
    outline: 0;
    padding: 3vmin;
    border: 0.5vmin solid #A4BFA2;
    border-radius: 3.5vmin;
    height: 9vmin;
    width: 64vmin;
    font-family: var(--font-family-Urbanist);
    font-size: 3.9vmin;
}

small {
    justify-content: end;
    display: grid;
    grid-column: 1/4;
    margin-right: 11vmin;
    cursor: pointer;
    font-family: var(--font-family-Urbanist);
    font-size: 2.9vmin;
    letter-spacing: 0.05vmin;
    text-decoration-line: underline;
    text-transform: capitalize;
    color: var(--color-text);
}

.remember_text {
    margin-left: 4vmin;
    display: grid;
    cursor: pointer;
    font-family: var(--font-family-Urbanist);
    font-size: 2.9vmin;
    letter-spacing: 0.05vmin;
    color: var(--color-text);
}

.remember_check {
    margin-top: 0.5vmin;
    margin-left: 10.8vmin;
    display: grid;
    width: 3.4vmin;
    height: 3.4vmin;
    cursor: pointer;
}

.btn_login {
    display: grid;
    grid-column: 1/4;
    margin-left: auto;
    margin-right: auto;
    background: linear-gradient(89.95deg, rgba(50, 177, 38, 0.6) 1.99%, rgba(36, 162, 233, 0.6) 85.34%, rgba(36, 162, 233, 0.6) 104.58%);
    padding-top: 1.7vmin;
    outline: 0;
    border-radius: 2.5vmin;
    border: none;
    font-family: var(--font-family-Comfortaa);
    font-size: 3.5vmin;
    text-transform: capitalize;
    cursor: pointer;
    color: #FFFFFF;
    height: 7.8vmin;
    width: 17vmin
}

.btn_login:hover {
    background-color: #32B126;
}

.dont_have_account {
    grid-column: 1/4;
    display: grid;
    margin-top: 5vmin;
    margin-left: auto;
    margin-right: auto;
    font-family: var(--font-family-Comfortaa);
    font-weight: 700;
    font-size: 2.9vmin;
    letter-spacing: 0.01vmin;
    text-transform: capitalize;
    cursor: pointer;
    color: var(--color-text);
}

.for_toggle_btn {
    animation-timing-function: cubic-bezier(0.41, 0.28, 0, 0.97);
    animation-duration: 1000ms;
}


.input_label_email {
    position: absolute;
    /* display: grid; */
    margin-top: 14.5vmin;
    margin-left: 87vmin;
    font-family: var(--font-family-Urbanist);
    font-size: 3.2vmin;
    letter-spacing: 0.05vmin;
    color: var(--color-text);
}

.input_label_password {
    display: grid;
    background: rgba(236, 236, 236, 0.34);
    backdrop-filter: blur(0.4vmin);
    left: 74vmin;
    top: 47.5vmin;
    font-family: var(--font-family-Urbanist);
    font-size: 3.2vmin;
    letter-spacing: 0.05vmin;
    color: var(--color-text);
}